.header {
	background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
	
	&__container {
		display: flex;
    flex-direction: column;
    justify-content: start;
    min-height: 230px;
		padding: 46px 20px 0 20px;
    margin: 0px auto;

		@media screen and (min-width: 768px){
			padding: 48px 75px 0 75px;
		}
	}

	&__bg-home {
		background-image: linear-gradient(
			rgba(0, 0, 0, 0.56),
			rgba(0, 0, 0, 0.56)
		),
		url('/images/header-home_mob_1x.jpg');

		@media screen and (min-device-pixel-ratio: 2),
     screen and (min-resolution: 192dpi),
     screen and (min-resolution: 2dppx),
     screen and (min-width: 320px) {
     background-image: linear-gradient(
			rgba(0, 0, 0, 0.56),
			rgba(0, 0, 0, 0.56)
		),
       url('/images/header-home_mob_2x.jpg');
   }

	  @media screen and (min-width: 768px) {
     background-image: linear-gradient(
			rgba(0, 0, 0, 0.56),
			rgba(0, 0, 0, 0.56)
			),	
       url('/images/header-home_tab_1x.jpg');
 
     @media screen and (min-device-pixel-ratio: 2),
       screen and (min-resolution: 192dpi),
       screen and (min-resolution: 2dppx),
       screen and (min-width: 768px) {
       background-image: linear-gradient(
			rgba(0, 0, 0, 0.56),
			rgba(0, 0, 0, 0.56)
			),	
       url('/images/header-home_tab_2x.jpg');
     }
   }

	 	@media screen and (min-width: 1024px) {
     background-image: linear-gradient(
			rgba(0, 0, 0, 0.56),
			rgba(0, 0, 0, 0.56)
			),	
       url('/images/header-home_desk_1x.jpg');
 
     @media screen and (min-device-pixel-ratio: 2),
       screen and (min-resolution: 192dpi),
       screen and (min-resolution: 2dppx),
       screen and (min-width: 1024px) {
       background-image: linear-gradient(
			rgba(0, 0, 0, 0.56),
			rgba(0, 0, 0, 0.56)
			),	
       url('/images/header-home_desk_2x.jpg');
     }
   }
	}
	&__myLibrary {
		background-image: linear-gradient(
			rgba(0, 0, 0, 0.56),
			rgba(0, 0, 0, 0.56)
		),
		url('/images/header-library_mob_1x.jpg');

		@media screen and (min-device-pixel-ratio: 2),
     screen and (min-resolution: 192dpi),
     screen and (min-resolution: 2dppx),
     screen and (min-width: 320px) {
     background-image: linear-gradient(
			rgba(0, 0, 0, 0.56),
			rgba(0, 0, 0, 0.56)
		),
       url('/images/header-library_mob_2x.jpg');
   }

	  @media screen and (min-width: 768px) {
     background-image: linear-gradient(
			rgba(0, 0, 0, 0.56),
			rgba(0, 0, 0, 0.56)
			),	
       url('/images/header-library_tab_1x.jpg');
 
     @media screen and (min-device-pixel-ratio: 2),
       screen and (min-resolution: 192dpi),
       screen and (min-resolution: 2dppx),
       screen and (min-width: 768px) {
       background-image: linear-gradient(
			rgba(0, 0, 0, 0.56),
			rgba(0, 0, 0, 0.56)
			),	
       url('/images/header-library_tab_2x.jpg');
     }
   }

	 	@media screen and (min-width: 1024px) {
     background-image: linear-gradient(
			rgba(0, 0, 0, 0.56),
			rgba(0, 0, 0, 0.56)
			),	
       url('/images/header-library_desk_1x.jpg');
 
     @media screen and (min-device-pixel-ratio: 2),
       screen and (min-resolution: 192dpi),
       screen and (min-resolution: 2dppx),
       screen and (min-width: 1024px) {
       background-image: linear-gradient(
			rgba(0, 0, 0, 0.56),
			rgba(0, 0, 0, 0.56)
			),	
       url('/images/header-library_desk_2x.jpg');
     }
   }
	}
}


.navigation {
	display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 45px;
}

.logo-text {
	display: none;
  font-weight: 500;
  font-size: 30px;
  line-height: 0.89;
	color: $color-main;
	transition: color 250ms cubic-bezier(0.4, 0, 0.2, 1);

	@media screen and (min-width: 768px) {
		display: inline;
	}
}
.svg {
	stroke: $color-main;
}

.logo-text:hover {
	color: $color-accent;
}

.form {
	position: relative;
	margin: 0px auto;

	&__input {
		background: transparent;
		border: none;
		border-bottom: 1px solid $color-main;
		width: 280px;
		margin-bottom: 20px;
		color: $color-main;
		&:hover,
		&:focus,
		&:active {
			outline: none;
			border-bottom: 1px solid $color-accent;
			color: $color-accent;
		}
		@media screen and (min-width: 768px) {
			width: 336px;
		}
	}
}
.label {
	color:$color-warning-accent;
	font-size: $secondary-font-size;
	line-height: 1.14;	
	font-weight: 400;
	margin: 0 auto;
}

.button {
	padding: 0;
	cursor: pointer;
	color: $color-main;
	background-color: transparent;
	border: none;
	&:focus {
		outline-color: $color-accent;
	}

	&__home {
		position: relative;
		line-height: 1.16;
		margin-right: 20px;

		@media screen and (min-width:768px) {
			margin-right: 40px;
		}
	}
	&__home:hover {
		color: $color-accent;
		transition: 0.3s;
	}
		&__myLibrary {
		position: relative;
		line-height: 1.16;
		margin-right: 20px;

		@media screen and (min-width:768px) {
			margin-right: 40px;
		}

		@media screen and (min-width:1024px) {
			margin-right: 70px;
		}

	}
	&__myLibrary:hover {
		color: $color-accent;
		transition: 0.3s;
	}

	&__submit {
		position: absolute;
		left: 265px;

		&:focus .svg {
			stroke: $color-accent;
		}
		&:hover .svg {
			stroke: $color-accent;
		}

		@media screen and (min-width: 768px) {
			left: 320px;
		}
	}

	&__watched {
		margin-right: 14px;
	}

	&__main {
		width: 130px;
		height: 44px;
		border-radius: 5px;
		border: 1px solid $color-main;
		transition: background-color 250ms cubic-bezier(0.4, 0, 0.2, 1), color 250ms cubic-bezier(0.4, 0, 0.2, 1), box-shadow 250ms cubic-bezier(0.4, 0, 0.2, 1);

		@media screen and (min-width: 768px) {
			width: 136px;
		}

		&:hover,
		&:focus {
			background-color: $color-accent;
			border: transparent;
			box-shadow: 0px 3px 1px rgba(0, 0, 0, 0.1), 0px 1px 2px rgba(0, 0, 0, 0.08),
  0px 2px 2px rgba(0, 0, 0, 0.12);
		}
	}
	&__current {
		color: $color-main;
		background-color: $color-accent;
		border: transparent;
		box-shadow: 0px 3px 1px rgba(0, 0, 0, 0.1), 0px 1px 2px rgba(0, 0, 0, 0.08),
  0px 2px 2px rgba(0, 0, 0, 0.12);
	}
}

.current-page::after {
  position: absolute;
  left: 0;
  bottom: -3px;
  content: '';
  width: 100%;
  height: 3px;
  background-color: $color-accent;
}

.nav-library {
	display: flex;
	justify-content: center;
	
}

.visually-hidden {
	display: none;
} 	